          console.log(row);
              axios({
                method: "POST",//
                url: "/api/flowtype",//
                params:{
                  name:row.Name,
                },
                // data: {
                //   dtid:row.DoctypeId,
                //   dsid1:row.FromStateId,
                //   daid:row.DocactionId,
                //   dsid2:row.ToStateId
                // }
